* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body{
    color: #fff;
    background-color: #1b1b32;
    font-family: 'Lato', sans-serif;
    margin-top: 40px;
}

h1{
    color: #fff;
    font-size: 35px;
    font-family: 'Roboto', sans-serif;
    text-align: center;
    margin: 20px 0;
    text-decoration: underline;
}

h2{
    text-align: center;
    padding: 20px;
    color: #1b1b32;
    font-family: 'Roboto', sans-serif;
}

.task-app{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.main-task-list{
    width: 600px;
    min-height: 500px;
    background-color: #d0d0d5;
    padding: 25px;
    margin: 10px;
    border-radius: 26px;
    -webkit-border-radius: 26px;
    -moz-border-radius: 26px;
    -ms-border-radius: 26px;
    -o-border-radius: 26px;
}